import axios from 'axios'

const apiKey = "563492ad6f91700001000001fae12a9229c04e8b9ac4824993c0a7ac"
const headers = {
    'Authorization': apiKey
}

const config = {
    baseUrl: "https://api.pexels.com/v1"
};

function fetchPictureList(page, perPage) {
    return axios.get(`${config.baseUrl}/curated?page=${page}&per_page=${perPage}`, {headers})
}

export {
    fetchPictureList
}